export async function POST(request) {
  const formData = await request.formData();

  const file = formData.get("commercial_invoice");

  if (!file) {
    return Response.json(
      { error: "No file received" },
      { status: 400 }
    );
  }

  return Response.json({
    success: true,
    fileName: file.name,
    fileType: file.type,
    fileSize: file.size,
  });
}
